<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row }" slot="2">
          <span style="color:#538FFF">{{row['2']}}</span>
        </template>
        <template slot-scope="{ row }" slot="reson">
          <button @click="lookReason(row)">查看原因</button>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="delayHear(row)">延期审理</button>
            <button @click="handleBack(row)">查看文件</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <!-- 同意撤回 延期审理 -->
    <Modal v-model="sureDialog" :footer-hide="true" title="延期审理">
      <Form ref="formValidate" :model="form" :rules="ruleValidate" :label-width="110">
        <FormItem label="起始日期" prop="startTime">
          <DatePicker type="date" placeholder v-model="form.startTime"></DatePicker>
        </FormItem>
        <FormItem label="结束日期" prop="endTime">
          <DatePicker type="date" placeholder v-model="form.endTime"></DatePicker>
        </FormItem>
        <FormItem label="延期事由" prop="reason">
          <Select v-model="form.reason">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="材料附件" prop="file">
          <div style="display:flex">
            <Upload action="//jsonplaceholder.typicode.com/posts/" :before-upload="handleUpload">
              <Button type="primary" size="small">浏览...</Button>
            </Upload>
            <div style="margin-left:10px;height: 40px;">{{form.file? form.file.name: '未选择文件'}}</div>
          </div>
        </FormItem>
        <FormItem label="备注信息" prop="remarks">
          <Input
            v-model="form.remarks"
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="请输入备注信息"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button type="primary" @click="submit">提交延期申请</Button>
      </div>
    </Modal>
    <!-- 驳回原因 -->
    <Modal v-model="backDilog" :footer-hide="true" title="驳回" ok-text="驳回">
      <Form ref="backFormValidate" :model="backForm" :rules="backFormRuleValidate" :label-width="0">
        <div style="margin-bottom:10px">请输入驳回原因：</div>
        <FormItem label prop="backInput">
          <Input
            v-model="backForm.backInput"
            :maxlength="100"
            show-word-limit
            type="textarea"
            placeholder="请输入原因"
            style="width:100%"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="backDilog=false">取消</Button>
        <Button type="primary" @click="backSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
const columns = [
  { title: "案号", slot: "2" },
  { title: "申请人" },
  { title: "代理人" },
  { title: "被申请人" },
  { title: "案件进度" },
  { title: "案件类型" },
  { title: "驳回原因", slot: "reson" },
  { title: "案件状态" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch
  },
  data() {
    return {
      searchData: {
        number: "",
        appeal: "",
        backAppeal: "",
        progress: [],
        regName: [],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backAppeal",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "progress",
          label: "案件进度",
          placeholder: "请选择案件进度",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: 10,
      columns: columns,
      tableData: [{ 1: 1, 2: 2 }],
      sureDialog: false,
      backDilog: false,
      form: {
        startTime: "",
        endTime: "",
        reason: "",
        file: "",
        remarks: ""
      },
      ruleValidate: {
        startTime: [
          {
            required: true,
            type: "date",
            message: "请选择日期",
            trigger: "change"
          }
        ],
        endTime: [
          {
            required: true,
            type: "date",
            message: "请选择日期",
            trigger: "change"
          }
        ],
        reason: [{ required: true, message: "请选择事由", trigger: "change" }],
        file: [
          {
            required: true,
            type: "object",
            message: "请上传文件",
            trigger: "change"
          }
        ],
        remarks: [
          { required: true, message: "请输入备注信息", trigger: "change" }
        ]
      },
      // 驳回
      backForm: { backInput: "" },
      backFormRuleValidate: {
        backInput: [
          { required: true, message: "请输入原因", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    goSearch() {},
    downloadExcel() {
      this.download();
    },
    // 同意撤回
    handleBack(row) {},
    //   查看原因
    lookReason(row) {
      this.backDilog = true;
    },
    // 重新生成文书
    delayHear(row) {
      this.sureDialog = true;
      this.$nextTick(() => {
        this.$refs["formValidate"].resetFields();
      });
    },
    handleUpload(file) {
      console.log(file);
      this.form.file = file;
      // console.log(this.form)
      this.$nextTick(() => {
        this.$refs["formValidate"].validate();
      });
      return false;
    },
    // 提交
    submit() {
      this.$refs["formValidate"].validate(valid => {
        console.log(this.form);
        if (valid) {
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    //   驳回提交
    backSubmit() {
      this.$refs["backFormValidate"].validate(valid => {
        console.log(this.backForm);
        if (valid) {
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    //处理文件流
    download(data) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", "留资详情.xls");
      document.body.appendChild(link);
      link.click();
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/.ivu-date-picker {
  width: 100%;
}
.wrap {
  .table_box {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .btn_box {
      text-align: right;
      margin-bottom: 20px;
    }
  }
}
</style>
